.date-picker-calender {
  .ui-widget.ui-widget-content {
    width: 100%;
    padding: 0;
    box-shadow: none;
    min-width: 100%;
  }
  .ui-datepicker th {
    padding-top: 15px;
  }
}

tr {
  background-color: white;
}
.ui-datepicker table {
  margin: 0;
}
.ui-widget.ui-widget-content {
  border: none;
  font-family: $body-font;
  padding: 20px;
  min-width: 300px;
  padding-top: 35px;
  z-index: 9999 !important;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.22);
}
.ui-datepicker th {
  color: var(--gray-300);
  font-size: 12px;
  line-height: 14px;
  font-weight: 600;
  padding-bottom: 15px;
  padding-top: 30px;
}
.ui-widget-header {
  background-color: transparent;
  border: none;
  padding: 0px;
  margin-bottom: 15px;
}
.ui-datepicker .ui-datepicker-title {
  font-size: 14px;
  line-height: 20px;
  color: var(--gray-900);
}
.ui-datepicker td a,
.ui-datepicker td span {
  text-align: center;
  padding: 8px, 0px, 8px, 0px;
  margin: 0px;
  border: none;
  background: transparent;
  font-size: 14px;
  color: var(--gray-600);
  line-height: 32px;
  border-radius: 4px;
}
.ui-button.ui-state-active:hover,
.ui-button:active,
.ui-state-active,
.ui-widget-content .ui-state-active,
.ui-widget-header .ui-state-active,
a.ui-button:active {
  background-color: var(--primary-500);
  border: none;
  color: white;
}
.ui-widget-content .ui-state-default {
  border: none;
  background: white;
  font-weight: normal;
  color: var(--gray-900);
}
.ui-state-highlight,
.ui-widget-content .ui-state-highlight,
.ui-widget-header .ui-state-highlight {
  border: none;
  background: var(--primary-500);
  color: white;
}

.ui-datepicker .ui-datepicker-prev {
  width: 40px;
  height: 40px;
  background-color: var(--primary-50);
  border-radius: 50%;
  border-color: var(--primary-50);
  cursor: pointer;
  top: -10px;
  &:after {
    content: "\f107";
    position: absolute;
    font-family: "Flaticon";
    left: 0px;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    transform: rotate(180deg);
    font-size: 16px;
    color: var(--primary-500);
  }
}
.ui-datepicker .ui-datepicker-next {
  width: 40px;
  height: 40px;
  background-color: var(--primary-50);
  border-radius: 50%;
  border-color: var(--primary-50);
  cursor: pointer;
  top: -10px;
  &:after {
    content: "\f107";
    position: absolute;
    font-family: "Flaticon";
    left: 0px;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-size: 16px;
    color: var(--primary-500);
  }
}
.ui-datepicker .ui-datepicker-prev span, .ui-datepicker .ui-datepicker-next span{
  display: none;
}